import Image from "next/image"
import Link from "next/link"

interface DonghuaCardProps {
  title: string
  image: string
  link: string
  type: string
  episode: string
  rating?: string
  priority?: boolean
}

export default function DonghuaCard({ 
  title, 
  image, 
  link, 
  type, 
  episode, 
  rating,
  priority = false 
}: DonghuaCardProps) {
  return (
    <Link href={link} className="group">
      <div className="bg-white rounded-lg shadow-lg overflow-hidden transform transition-transform duration-300 hover:scale-[1.02]">
        <div className="relative h-[200px]">
          <Image
            src={image}
            alt={title}
            fill
            sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 25vw"
            className="object-cover"
            priority={priority}
          />
          <div className="absolute top-2 left-2">
            <span className="bg-black text-white px-2 py-1 text-xs rounded">
              {type}
            </span>
          </div>
          <div className="absolute top-2 right-2">
            <span className="bg-black text-white px-2 py-1 text-xs rounded">
              Ep {episode}
            </span>
          </div>
          <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black to-transparent p-4">
            <h3 className="text-white font-semibold line-clamp-2">
              {title}
            </h3>
          </div>
        </div>
        <div className="p-4">
          <div className="flex justify-between items-center">
            {rating ? (
              <span className="text-sm text-gray-600">Rating: {rating}</span>
            ) : (
              <span className="text-sm text-gray-600">Episode {episode}</span>
            )}
            <span className="text-sm font-medium bg-black text-white px-2 py-1 rounded">
              Sub
            </span>
          </div>
        </div>
      </div>
    </Link>
  )
}
